import { FlowApiShowFullPath } from "src/helpers/Flows/Api"

export const parseResponseButtonApi = function (
  res: any,
  { target = "_blank", autoOpenLink = false } = {}
) {
  if (res && res.Data) {
    FlowApiShowFullPath({
      response: res,
      configFullPath: res.Data,
      target,
      autoOpenLink,
    })
  }
}
